#board {
    width: 50%;
    border: 1px solid;
    border-radius: 5px;
    margin-top: 5%;
    background-color: #676769c9;
    margin-right: 25%;
    margin-left: 25%;
}
.grid-row {
    height: 23%;
    width: 100%;
}
.grid-row:first-child {
    margin-top: 3.5%;
}
.grid-row:last-child {
    margin-bottom: 0;
}
.grid-cell {

    width: 20%;
    height: 88%;
    float: left;
    border-radius: 5px;
    background: darkgray;
    margin-right: 4%;
    margin-bottom: 4%;
}
.grid-cell:first-child {
    margin-left: 4%;
}
.grid-cell:last-child {
    margin-bottom: 0;
}



.one-cell {
    width: 100%;
    height: 100%;
    border-radius: 5px;
}
.one-cell-number {
    font-weight: bold;
    text-align: center;
}
